* {
  margin: 0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Dancing Script', cursive;
  cursor: url("./aseets/icons/cursor.svg"),auto;
}

body {
  background-image:  url("./aseets/background.jpg");
  background-position: center;
  min-height: 100vh;
  padding: 1em 2em;
  @media screen and (max-width: 800px) {
    padding: 1em;
  }
}

button {
  background: none;
  outline: none;
  bordeR: none;
}

h1 {
  font-size: 3em;
  padding: 0.1em 0.7em;
  border-bottom: 2px solid #000;
  margin-bottom: 1em;
  text-align: center;
  @media screen and (max-width: 800px) {
    font-size: 2em;
  }
}

.icon {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  @media screen and (max-width: 800px) {
    width: 30px;
    height: 30px;
  }
  object-fit: contain;
  transition: all 0.5s linear;
  margin: 3px;
  background-color: rgba(255, 255, 255, 0.4);
  &:hover {
    background-color: rgba(255, 255, 255, 0.8);
  }
  &.red {
    background-color: rgba(255, 0, 0, 0.4);
    &:hover {
      background-color: rgba(255, 0, 0, 0.8);
    }
  }
  &.green {
    background-color: rgba(60, 179, 113, 0.4);
    &:hover {
      background-color: rgba(60, 179, 113, 0.8);
    }
  }
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2em 0;
  @media screen and (max-width: 800px) {
   flex-direction: column;
   .addTasks {
    width: 50px;
    height: 50px;
   }
  }
  .input-txt {
    background-color: transparent;
    border: none;
    outline: none;
    border-bottom: 2px solid rgba(0,0,0,0.5);
    margin: 1em;
    font-size: 1.5em;
    transition: all 0.5s linear;
    &:hover {
      background-color: rgba(255,255,255,0.6);
    }
    @media screen and (max-width: 800px) {
      font-size: 1.3em;
    }
  }
  .filter {
    font-size: 1.3em;
    text-align: center;
  }
  select {
    border: none;
    background-color: transparent;
 
  }
}


.task-item {
  font-size: 2em;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.2em;
  width: 80%;
  margin: 0 auto;
  @media screen and (max-width: 800px) {
    font-size: 1.2em;
    width: 100%;
  }
  li {
    margin-right: 1em;
    width: 40%;
  }
  .completed {
   text-decoration: line-through;
   color: rgba(0,0,0,0.3);
  }
}


